body {
  background: #E9E9E9; }

h1, h2, h3, h4, h5, h6 {
  font-weight: 300; }

nav {
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.22);

  .navbar-brand {
    color: #BD1E23 !important;
    font-weight: bold; }

  a:hover {
    text-decoration: none; } }

#pins {
  margin: 0 auto;
  width: 100%;
  .box {
    margin: 10px;
    width: 300px;
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.22);
    border-radius: 7px;
    text-align: center;
    overflow: hidden;
    img {
      max-width: 100%;
      height: auto; }
    h2 {
      font-size: 22px;
      margin: 0;
      padding: 25px 10px;
      a {
        color: #474747;
        text-decoration: none; } }
    .user {
      font-size: 12px;
      border-top: 1px solid #EAEAEA;
      padding: 15px;
      margin: 0; } } }

#edit_page {
  .current_image {
    img {
      display: block;
      margin: 20px 0; } } }

#pin_show {
  .panel-heading {
    padding: 0; }
  .pin_image {
    img {
      max-width: 100%;
      width: 100%;
      display: block;
      margin: 0 auto; } }
  .panel-body {
    padding: 35px;
    h1 {
      margin: 0 0 10px 0; }
    .description {
      color: #868686;
      line-height: 1.75;
      margin: 0; } }
  .panel-footer {
    padding: 20px 35px;
    p {
      margin: 0; }
    .user {
      padding-top: 8px; } } }

textarea {
  min-height: 250px; }
